
.btn-dashboard {
  width:120px;
  height:105px;
  padding: 10px;
  border-radius: 7px;
}

.accordion-tab-base{
  margin-bottom: 0;
}

.accordion-tab-base>.tab-content{
  color: #eee;
  background-color: rgba(66, 91, 122, 0.43);
  border-top: 1px solid #666;
  border-right: 1px solid #666;
  border-bottom: 1px solid #666;

}

.accordion-tab-base>.nav-tabs>li.active>a,
.accordion-tab-base>.nav-tabs>li.active>a:focus,
.accordion-tab-base>.nav-tabs>li.active>a:hover {
  color: #eee;
  cursor: default;
  background-color: rgba(66, 91, 122, 0.43);
  border-top: 1px solid #666;
  border-left: 1px solid #666;
  border-bottom: 1px solid #666;
}

a.list-group-item,
a.list-group-item:hover,
a.list-group-item:focus,{
  color: #333;
}
a.list-group-item:hover,{
  background-color: #f5f5f5;
}

.project-message-body{
  background-color: transparent;
  border: none;
  resize: none;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
}

.panel .timeline{
  background: transparent;
}
.timeline{
  padding-bottom: 0;
}
.timeline:after {
   left: 0;
   width: 0;
   height: 0;
   border-radius: 0;
}
.timeline-entry {
  margin-bottom: 15px;
}
.timeline-label a.btn-link, .timeline-label a.btn-link:active{
  color: #337ab7;
  text-decoration: none;
}

input:focus, textarea:focus {
  outline: thin #266fb0 solid;
  outline-width: 1px;
}
.display-flex{display: flex;}
